// @flow
// libs
import React, { memo } from 'react';
import CheckBox from 'commons/components/CheckBox';

type Props = {
  paramsSearch: Object,
  handleChangeSearch: Function,
};

export const GroupCompareChart = ({
  paramsSearch,
  handleChangeSearch,
}: Props) => (
  <div className="group-char-checkbox">
    <div className="checkbox-header"> </div>
    <div className="list-checkbox">
      <CheckBox
        name="generation"
        isChecked={paramsSearch?.generation}
        label=""
        id="generation"
        handleToggleCheckbox={() =>
          handleChangeSearch(paramsSearch?.generation, 'generation')
        }
      />
      <CheckBox
        name="insolation"
        id="insolation"
        isChecked={paramsSearch?.insolation}
        label=""
        handleToggleCheckbox={() =>
          handleChangeSearch(paramsSearch?.insolation, 'insolation')
        }
      />
      <CheckBox
        name="performance"
        id="performance"
        isChecked={paramsSearch?.performance}
        label=""
        handleToggleCheckbox={() =>
          handleChangeSearch(paramsSearch?.performance, 'performance')
        }
      />
    </div>
  </div>
);

export default memo<Props>(GroupCompareChart);
